import React, { useEffect, useState } from "react";
import { getProductById } from "../../api/products";
import CartButtons from "../../components/CartButtons.jsx/CartButtons";
import ProductAmount from "./ProductAmount";
import { SCProduct } from "./style";

const ShoppingCartProduct = ({ id, amount, addMemoized, removeMemoized }) => {
  const [product, setProduct] = useState();

  useEffect(() => {
    async function fetchData() {
      const res = await getProductById(id);
      setProduct(res);
    }
    fetchData();
  }, [id]);

  return (
    <>
      <SCProduct src={product?.src} className="unselectable">
        <div className="title">{product?.title}</div>
        <div className="img" />
        <ProductAmount amount={amount} price={product?.price} />
        <CartButtons
          id={product?.id}
          price={product?.price}
          extraStyling={{
            padding: "6px",
            alignSelf: "center",
          }}
          addToCart={addMemoized}
          removeFromCart={removeMemoized}
        />
      </SCProduct>
    </>
  );
};

export default React.memo(ShoppingCartProduct);
